body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.todoContainer {
  border-radius: 0.5rem;
  width: 25vw;
  height: 75vh;
  margin: 0 auto;
  overflow: auto;
}

.taskContainer p {
  background: #000;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: background 0.5s;
  overflow: auto;
  user-select: none;
}

::-webkit-scrollbar {
  width: 0;
}
.taskContainer p:hover {
  background: rgb(19, 18, 18);
  border-radius: 0.2rem;
}

.task {
  background: #000;
  border: none;
}

.task {
  background: transparent;
  border: none;
}
.task:focus {
  background: transparent;
  outline: none;
}

.taskStatus {
  text-decoration: line-through;
}

.taskStatus input {
  text-decoration: line-through;
}
#newTask input {
  border-radius: 0.2rem;
  background: rgb(90, 90, 90);
  color: #fff;
  border: none;
}

#newTask input:focus {
  outline: none;
  box-shadow: 0 0 4px 1px #ffc107;
}

::placeholder {
  color: #ccc;
}

@media screen and (max-width: 1400px) {
  .todoContainer {
    width: 30vw;
  }
}
